h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

picture {
  border: none;
  margin: 0;
}

b {
  display: block;
}

button {
  cursor: pointer;
  padding: 0;
}

a {
  color: inherit;
  cursor: pointer;
  font-style: normal;
  text-decoration: none;
}

html {
  scroll-behavior: smooth;
}

button:focus, button:active, input:focus, input:active, select:focus, select:active, textarea:focus, textarea:active, a:focus, a:active, svg:focus, svg:active {
  outline: none;
}

body {
  color: #5c6d7d;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
}

.container {
  max-width: 1520px;
  margin: 0 auto;
}

.cart-button {
  position: relative;
}

.shop__card {
  display: none;
  position: relative;
}

.shop__card .shop__card-bgc {
  z-index: 20;
  filter: drop-shadow(0 10px 80px #0034294d);
  width: 338px;
  height: 278px;
  position: absolute;
  top: 15px;
  right: -45px;
}

.shop__card .shop__card-info {
  z-index: 30;
  flex-direction: column;
  align-items: start;
  width: 338px;
  height: 258px;
  padding: 30px;
  display: flex;
  position: absolute;
  top: 28px;
  right: -100px;
}

.shop__card .shop__card-text {
  color: #5c6d7d;
  font-size: 18px;
  line-height: 1.6;
}

.shop__card .shop__card-quantity {
  color: #33485d;
  font-size: 18px;
  font-weight: 700;
}

.shop__card .shop__card-currency {
  color: #33485d;
  font-size: 18px;
  font-weight: 400;
}

.cart-button:hover .shop__card {
  display: block;
}

.order__btn-hover {
  color: #fff;
  background-color: #47b39c;
  border-top-left-radius: 10px;
  border-bottom-right-radius: 10px;
  align-items: center;
  margin-top: 30px;
  padding: 15px 21px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.2;
  transition: background-color .3s;
  display: flex;
}

.order__btn-hover:hover {
  background-color: #ffa800;
}

.shop__card-edit {
  color: #33485d;
  border-bottom: 1px solid #5c6d7d;
  margin-top: 30px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
  transition: color .3s;
}

.shop__card-edit:hover {
  color: #ffa800;
  border-bottom: 1px solid #ffa800;
}

.header__container {
  align-items: center;
  padding: 20px 40px;
  display: flex;
}

.header__container-first {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__box-second {
