  text-transform: uppercase;
  border-bottom: 1px solid #0000;
  padding-bottom: 7px;
  font-weight: 700;
  line-height: 1.2;
}

.header__phone:hover {
  color: #33485d;
  border-bottom: 1px solid #33485d;
}

.header__phone:active {
  color: #47b39c;
  border-bottom: 1px solid #47b39c;
}

.lang__list {
