  content: "";
  background-color: #dde8f0;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  margin-left: 10px;
  margin-right: 10px;
  display: inline-block;
}

.lang__link:hover {
  color: #33485d;
  border-bottom: 1px solid #33485d;
}

.lang__link:active {
  color: #47b39c;
  border-bottom: 1px solid #47b39c;
}

.header__bnt-login {
  color: #33485d;
  background-color: inherit;
  border: none;
  border-bottom: 1px solid #0000;
  align-items: center;
  margin-left: 62px;
  padding-bottom: 7px;
  font-weight: 700;
  line-height: 1.2;
  display: flex;
}

.header__bnt-login svg {
  fill: #ffbe48;
