}

.header__bnt-login:hover {
  color: #33485d;
  border-bottom: 1px solid #33485d;
}

.header__bnt-login:active {
  color: #47b39c;
  border-bottom: 1px solid #47b39c;
}

.header__bnt-login:active svg {
  fill: #47b39c;
}

.header__container-second {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.header__container-top {
  justify-content: start;
  align-items: center;
  margin-bottom: 26px;
  padding-right: 17px;
  display: flex;
}

.header__category-block {
  background-color: #ffbe48;
  border-top-left-radius: 10px;
  align-items: center;
  width: 331px;
  margin-top: 20px;
  padding: 20px;
