  position: relative;
}

.header__btn-menu svg {
  margin-right: 20px;
}

.header__btn-menu:hover {
  transform: scale(1.05);
}

.icon__close {
  fill: #fff;
  width: 30px;
  height: 25px;
}

.hidden {
  display: none;
}

.header__btn-category {
  background-color: inherit;
  color: #fff;
  border: none;
  margin-left: 83px;
