  display: block;
  position: relative;
}

.header__btn-category:after {
  content: "";
  opacity: .5;
  background-color: #fff;
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: -10px;
  left: 0;
}

.header__btn-category:hover {
  transform: scale(1.05);
}

.catalog__items {
  opacity: 0;
  visibility: hidden;
  z-index: 20;
  background: #fff;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  flex-direction: column;
  width: 361px;
  padding: 5px;
  transition: opacity .6s ease-in-out, visibility .6s ease-in-out;
  position: absolute;
  top: 69px;
  left: 0;
  box-shadow: 0 10px 80px #0034294d;
}

.catalog__items-active {
  opacity: 1;
  visibility: visible;
}

.catalog__item-main {
  align-items: center;
  display: flex;
}

.catalog__item {
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding-left: 10px;
  display: flex;
}

.catalog__item .catalog__item-text {
  color: #5c6d7d;
  margin-left: 16px;
  font-size: 18px;
  font-weight: 500;
  line-height: 3.8;
}

.catalog__item .categories-items {
  fill: #5c6d7d;
}

.catalog__item .categories-arrows {
  fill: #5c6d7d;
  margin-right: 25px;
}

.catalog__item:hover {
  background-color: #dde8f0;
}

.catalog__item:hover .catalog__item-text {
  color: #33485d;
}

.catalog__item:hover .categories-arrows, .catalog__item:hover .categories-items {
  fill: #33485d;
}

.header__search-form {
  background-color: #dde8f0;
  border-radius: 10px;
  margin-right: 15px;
  position: relative;
}

.header__search-input {
  background-color: inherit;
  color: #33485d;
  border: 2px solid #0000;
  border-radius: 10px;
  outline: none;
  width: 100%;
  height: 50px;
  padding-left: 19px;
  font-size: 16px;
  line-height: 1.2;
}

.header__search-input::placeholder {
  color: #33485d;
  font-size: 16px;
  line-height: 1.2;
}

.header__search-input:focus {
  border: 2px solid #33485d;
}

.header__search-button {
  padding: 16px 12px;
  transition: transform .5s;
  position: absolute;
  top: 4px;
  right: -20px;
}

.header__search-button:hover, .header__search-button:focus {
  transform: scale(1.3);
}

.header__favorite-list {
  align-items: center;
  display: flex;
}

.header__favorite-btn {
  background-color: inherit;
  border: 2px solid #ffcb6d;
  border-left: none;
  border-right: none;
  padding: 12.5px 30px;
}

.header__favorite-btn:nth-child(odd) {
  border: 2px solid #ffcb6d;
}

.header__favorite-btn:hover {
  fill: #fff;
  background-color: #47b39c;
  border-top: 2px solid #47b39c;
  border-bottom: 2px solid #47b39c;
}

.header__favorite-btn:hover:nth-child(odd) {
  border: 2px solid #47b39c;
}

.cart-button {
  fill: #33485d;
  border-top-right-radius: 10px;
}

.cart-button:hover {
  fill: #fff;
  background-color: #47b39c;
  border: 2px solid #47b39c;
}

.cart-button:hover span {
  background-color: #fff;
  border: 2px solid #fff;
}

.icon-shop-card {
  position: relative;
}

.shop__card-badge {
  color: #33485d;
  background: #ffbe48;
  border: 2px solid #ffbe48;
  border-radius: 18px;
