}

.total-reviews:hover {
  border-bottom: 1px solid #5c6d7d80;
}

.artical {
  color: #5c6d7d;
  font-weight: 500;
  line-height: 1.2;
}

.artical span {
  color: #33485d;
}

.sale__box-part1 {
